}
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1faee;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #66f0f1, #0b0c10);
  background: linear-gradient(180deg, var(--SkyBlue), var(--Black));
  border-radius: 30px;
  box-shadow: inset 2px 2px 2px hsl(0deg 0% 100% / 25%),
    inset -2px -2px 2px rgb(0 0 0 / 25%);
}

