     color: #696969;
     margin:20px;
     border: 5px solid;
     border-radius: 25px;
     box-shadow: 0px 2px 18px -4px rgb(64, 64, 64);
     transition: -webkit-transform .5s;
     transition: transform .5s;
     transition: transform .5s, -webkit-transform .5s;
}
.tags .card:hover {
  background-color: aliceblue;
  border: 5px solid #66f0f1;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
 .tags .card___title{
     margin-top: 0;
     font-size: 20px;
     font-weight: 600;
     letter-spacing: 1.2px;
     justify-content: center;
}
 .tags .card__content{
    padding: 1em 0em;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}
.tags .card__text{
    background-color: #007bff;
    color: #fff;
    padding: .2em .5em;
    border-radius: 4px;
    margin-left: .5em;
    transition: all .1s;
    cursor: pointer;
    margin-top: .5em;
}
.tags .card-btn{
     all: unset;
     display: block;
     margin-left: auto;
     border: 2px solid #a6a6a6;
     padding: 10px 15px;
     border-radius: 25px;
     font-size: 15px;
     font-weight: 600;
     transition: all 0.5s;
     cursor: pointer;
     letter-spacing: 1.2px;
     margin-top: 35px;
    color: #2a9fa5;
}
 .tags .card-btn:hover{
    color:#02b875;
    background: #FFF;
}


.back-to-top {
    position: fixed;
    width: 50px;
    height: 50px;
    border-radius: 50px;
    right: 15px;
    bottom: 25px;
    background:  rgb(11, 11, 56);
    color: #fff;
    transition: display 0.5s ease-in-out;
    z-index: 99999;
    
}
.back-to-top:hover{
    background-color: rgb(3, 3, 95);
    box-shadow: 0 6px 6px 0px rgb(2, 2, 139);
}
.scrollButton {
    z-index: 200;
    position: fixed;
    bottom: 0;
    transition: opacity 0.3s ease;
   
}


